header{
    height: 100vh;
    padding: 5rem;
    overflow: hidden;
}
.header_container{
    text-align: center;
    height: 100%;
    position: relative;
}


.cta{
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}

/* socials */
.header_socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    left: 0;
    position: absolute;
    bottom: 3rem;
}
.header_socials::after{
    content: '';
    width: 1px ;
    height: 2rem;
    background: var(--color-primary);
}
/* image */
.me{
    background: linear-gradient(var(--color-primary), transparent);
    width: 12rem;
    height: 30rem;
    position: absolute;
    left: calc(50% - 6rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: 1rem 0.5rem 0.5rem 0.5rem;
}
.scroll_down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 0.9rem;

}
/* media query */
@media screen and (max-width: 1024px) {
    header{
        height: 68vh;

    }
}
/* media query for mobile */
@media screen and (max-width: 600px) {
    header{
        height: 100vh;
    }
    .header_socials, .scroll_down{
        display: none;
    }
}


